.signup{
  font-family: cursive;
  font-style: italic;
  width: 350px;
  margin: 12vh auto;
  padding: 20px;
  border-radius: 40%;
  border: 5px solid green;
   background-color:black;
  display:flex;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  vertical-align: middle;
  align-content: center;
  color: wheat;
  
}

.signup h1{
  text-align: center;
}

.signup input{
  margin-top: 20px;
  border-radius: 5px;
  line-height: 2em;
  width: 200px;
  border-bottom: 1px solid ;
  border: 2px solid green;
  font-size: large;
}

.signup input:focus{
  outline: none ;
  border: 2px solid mediumblue;
  box-shadow: 0 0 5px mediumblue;
}


.signup button{
  display: block;
  
  border:none;
  height: 25px;
  width: 80px;
  padding: 5px;
  background-color: greenyellow;
  width: 180px;
  margin: 0 auto;
  font-size: medium;
  height: 2rem;
  margin-top: 20px;
}


.errorText{
  color:red;
  
  font-family: cursive;
  font-weight: bold;
}

.signupLink{
  display: block;
  text-decoration: none;
  font-family:cursive;
  font-style: italic;
color:wheat;
  margin-top: 20px;
  text-align: right;
  font-weight:bold;
}